
import { Component, Vue } from 'vue-property-decorator'
import { base, images } from '@/store'
import { Archetype } from '../@types/archetype'
import {
  PersonalityPart,
  UserAction,
} from '../@types/types'

import NextButton from '@/components/NextButton.vue'

@Component({
  components: { NextButton },
})
export default class SelfAnswers extends Vue {
  selected: Archetype | null = null

  get archetypes(): string[] {
    return Object.keys(Archetype)
  }

  imagesLoaded = false
  get imgs() {
    return images.covers
  }

  select(arch: Archetype) {
    if (this.selected !== arch) this.selected = arch
    else this.selected = null
  }

  next() {
    if (this.selected) {
      base.submitAction({
        type: UserAction.answer_question,
        info: this.selected,
        changes: [
          [PersonalityPart.self, [this.selected, 0]],
        ],
      })
    }
  }
  mounted() {
    base.setBackgroundColor('white')

    let unloadedImages = Object.keys(this.imgs).length

    for (const arch in this.imgs) {
      const image = new Image()
      const archetype = arch as Archetype
      image.src = this.imgs[archetype]
      image.onload = () => {
        unloadedImages--
        if (!unloadedImages) this.imagesLoaded = true
      }
    }
  }
}
