
import {
  Component,
  Vue,
  Prop,
} from 'vue-property-decorator'
import { base } from '@/store'

@Component
export default class NextButton extends Vue {
  @Prop({ type: Function, default: () => undefined })
  onClick!: () => undefined

  @Prop({ type: Boolean, default: false })
  disabled!: boolean

  get colored(): boolean {
    return (
      base.backgroundColor !== 'white' &&
      base.backgroundColor !== 'black'
    )
  }

  get color(): string {
    return this.colored ? base.backgroundColor : 'white'
  }
}
