
import {
  Component,
  Vue,
  Watch,
} from 'vue-property-decorator'
import { base } from '@/store'

import {
  Change,
  Page,
  PersonalityPart,
  State,
  Summary,
  UserAction,
} from '@/@types/types'

import { Archetype } from '@/@types/archetype'

@Component
export default class App extends Vue {
  get backgroundColor(): string {
    return base.backgroundColor
  }
  get pageComponent(): any {
    return base.currentPageConfig.component
  }
  get isStarted(): boolean {
    return !!base.summary.user_data.email
  }
  get isFree(): boolean {
    return !!base.freeMode
  }

  isVisualization = false

  mounted() {
    // try to load existing data
    const prevStateString = window.localStorage.getItem(
      'ourchetypeState'
    )

    if (prevStateString && prevStateString.length > 0) {
      // confirm restart
      var r = confirm(
        'Anda yakin mau mengulang proses dan menghapus progres sejauh ini?'
      )
      if (!r) {
        const prevState: {
          state: State
          summary: Summary
        } = JSON.parse(prevStateString)

        base.submitForm(prevState.summary.user_data)

        const changes: Change[] = []
        const state = prevState.state
        for (const arch in state.persona_scores) {
          const archetype = arch as Archetype
          const score = state.persona_scores[archetype]
          if (score)
            changes.push([
              PersonalityPart.persona,
              [archetype as Archetype, score],
            ])
        }
        const topArchetypes = state.top_archetypes
        if (topArchetypes.shadow)
          changes.push([
            PersonalityPart.shadow,
            [topArchetypes.shadow, 0],
          ])
        if (topArchetypes.self)
          changes.push([
            PersonalityPart.self,
            [topArchetypes.self, 0],
          ])

        base.submitAction({
          type: UserAction.revisit_page,
          page: prevState.state.page,
          page_detail: prevState.state.page_detail,
          changes,
        })
      }
    }

    this.isVisualization = !!window.location.href.match(
      /#viz/
    )
    if (this.isVisualization) return

    const isFree = !!window.location.href.match(/#free/)
    if (isFree) {
      const vals: {
        page?: Page | null
        persona?: Archetype
        shadow?: Archetype
        self?: Archetype
      } = {
        page: null,
      }

      // Page
      const pageRes = window.location.href.match(
        /#page:([a-z|_|0-9]+)/
      )
      if (pageRes) vals.page = pageRes[1] as Page

      // Personality
      const persona = window.location.href.match(
        /#persona:([a-z|A-Z|_|0-9]+)/
      )

      if (persona) vals.persona = persona[1] as Archetype

      const shadow = window.location.href.match(
        /#shadow:([a-z|A-Z|_|0-9]+)/
      )
      if (shadow) vals.shadow = shadow[1] as Archetype

      const self = window.location.href.match(
        /#self:([a-z|A-Z|_|0-9]+)/
      )
      if (self) vals.self = self[1] as Archetype

      base.freeSkip(vals)
    }
  }

  // private showCloseWarning(e: BeforeUnloadEvent) {
  //   var confirmationMessage =
  //     'Progress akan dihapus saat page ditutup.' +
  //     'Anda yakin mau menutup page ini?'
  //   ;(e || window.event).returnValue = confirmationMessage //Gecko + IE
  //   return confirmationMessage //Gecko + Webkit, Safari, Chrome etc.
  // }

  // @Watch('isStarted', { immediate: true })
  // setWarning(val: boolean) {
  //   if (!this.isFree && val)
  //     window.addEventListener(
  //       'beforeunload',
  //       this.showCloseWarning
  //     )
  //   else
  //     window.removeEventListener(
  //       'beforeunload',
  //       this.showCloseWarning
  //     )
  // }
  @Watch('pageComponent')
  scrollToTop() {
    const c =
      document.documentElement.scrollTop ||
      document.body.scrollTop
    if (c > 0) {
      window.requestAnimationFrame(this.scrollToTop)
      window.scrollTo(0, c - c / 8)
    }
  }
}
