
import { Component, Vue } from 'vue-property-decorator'
import { base, images } from '@/store'
import { Area, UserAction } from '../@types/types'
import { ArchetypeColor, Color } from '../@types/archetype'

import NextButton from '@/components/NextButton.vue'

@Component({
  components: { NextButton },
})
export default class Conclusion extends Vue {
  get pageConfig() {
    return base.currentPageConfig
  }
  get area() {
    return this.pageConfig && this.pageConfig.area
  }

  get imgs() {
    switch (this.area) {
      case Area.persona:
        return images.personas
      case Area.shadow:
        return images.shadows
      default:
        return images.selves
    }
  }
  get topArchetype() {
    if (this.area && base.currentState) {
      switch (this.area) {
        case Area.persona:
          return base.summary.top_archetypes.persona
        case Area.shadow:
          return base.summary.top_archetypes.shadow
        case Area.self:
          return base.summary.top_archetypes.self
      }
    }
    return null
  }
  get image() {
    if (this.topArchetype)
      return this.imgs[this.topArchetype]
    return null
  }
  next() {
    // submit to state history
    base.submitAction({
      type: UserAction.press_next,
    })
  }
  get color() {
    let color = 'white'
    if (this.area != 'shadow') {
      if (this.topArchetype) {
        color = Color[ArchetypeColor[this.topArchetype]]
      }
    } else {
      color = 'black'
    }
    return color
  }
  mounted() {
    base.setBackgroundColor(this.color)
  }
}
