
import { Component, Vue } from 'vue-property-decorator'
import { base, images } from '@/store'
import {
  ArchetypeName,
  ShadowName,
} from '@/@types/archetype'
import descriptions from '@/configs/descriptions'

@Component
export default class EndConclusion extends Vue {
  disclaimer =
    'Ini adalah <b>simulasi</b> dari pemilihan secara mendasar ' +
    'saja, sehingga hasilnya <b>tidak mutlak</b> dan bukan ' +
    'merupakan hasil psikotes. Karakter dalam dirimu akan ' +
    'selalu berkembang dan setiap archetype bisa muncul ' +
    'di tiap waktu yang berbeda.'

  get topArchetypes() {
    return base.summary.top_archetypes
  }

  get name() {
    return base.summary.user_data.name || 'Nama'
  }

  archetypeName = ArchetypeName
  shadowName = ShadowName

  get persona() {
    return this.topArchetypes.persona
  }
  get shadow() {
    return this.topArchetypes.shadow
  }
  get self() {
    return this.topArchetypes.self
  }

  get personaImage() {
    return images.personas[this.persona]
  }
  get shadowImage() {
    return images.shadows[this.shadow]
  }
  get selfImage() {
    return images.selves[this.self]
  }

  get personaDescription() {
    return descriptions[this.persona].persona
  }
  get shadowDescription() {
    return descriptions[this.shadow].shadow
  }
  get selfDescription() {
    return descriptions[this.self].self
  }

  download(el: HTMLAnchorElement) {
    if (this.imagesLoaded) {
      const canvas = this.$refs.canvas as HTMLCanvasElement
      const ctx = canvas.getContext('2d')
      if (ctx) {
        ctx.fillStyle = '#ffffff'
        ctx.fillRect(0, 0, 1080, 1920)
        this.drawImage(ctx, this.$refs.top, 63, 63, 957, 87)
        ctx.fillStyle = '#d3d3d3'
        ctx.fillRect(63, 63 + 63, 957, 1674 - 63)
        this.drawImage(
          ctx,
          this.$refs.title,
          115,
          163,
          383,
          183
        )
        this.drawImage(
          ctx,
          this.$refs.icons,
          924,
          163,
          39,
          172
        )
        ctx.font = 'italic 60px Raleway'
        ctx.fillStyle = 'black'
        ctx.textAlign = 'right'
        ctx.fillText(
          ArchetypeName[this.persona],
          896,
          163 + 40
        )
        ctx.fillText(ShadowName[this.shadow], 896, 230 + 40)
        ctx.fillText(
          ArchetypeName[this.self],
          896,
          298 + 40
        )
        this.drawImage(
          ctx,
          this.$refs.shadowText,
          843,
          946,
          174,
          324
        )
        this.drawImage(
          ctx,
          this.$refs.shadow,
          627,
          1049,
          277,
          277
        )
        this.drawImage(
          ctx,
          this.$refs.selfText,
          187,
          1187,
          103,
          237
        )
        this.drawImage(
          ctx,
          this.$refs.self,
          253,
          1049,
          547,
          493
        )
        this.drawImage(
          ctx,
          this.$refs.personaText,
          113,
          608,
          112,
          232
        )
        this.drawImage(
          ctx,
          this.$refs.persona,
          186,
          525,
          678,
          666
        )
        ctx.fillStyle = '#707171'
        ctx.fillRect(63, 1541, 957, 143)

        ctx.font = '83px Raleway'
        ctx.fillStyle = 'white'
        ctx.textAlign = 'center'
        ctx.fillText(
          this.name.toUpperCase(),
          1080 / 2,
          1638
        )

        this.drawImage(
          ctx,
          this.$refs.footer,
          63,
          1682,
          957,
          179
        )

        /// create an "off-screen" anchor tag
        var lnk = document.createElement('a'),
          e

        /// the key here is to set the download attribute of the a tag
        lnk.download = 'My Archetype.png'

        /// convert canvas content to data-uri for link. When download
        /// attribute is set the content pointed to by link will be
        /// pushed as "download" in HTML5 capable browsers
        lnk.href = canvas.toDataURL('image/png;base64')

        /// create a "fake" click-event to trigger the download
        if (document.createEvent) {
          e = document.createEvent('MouseEvents')
          e.initMouseEvent(
            'click',
            true,
            true,
            window,
            0,
            0,
            0,
            0,
            0,
            false,
            false,
            false,
            false,
            0,
            null
          )

          lnk.dispatchEvent(e)
        }
        else if ((lnk as any).fireEvent) {
          (lnk as any).fireEvent('onclick')
        }
      }
    }
  }

  drawImage(
    ctx: any,
    img: any,
    x: number,
    y: number,
    width: number,
    height: number
  ) {
    ctx.drawImage(
      img,
      0,
      0,
      img.naturalWidth,
      img.naturalHeight,
      x,
      y,
      width,
      height
    )
  }

  imagesLoaded = false

  mounted() {
    base.setBackgroundColor('white')
    base.endRun()

    const images: HTMLImageElement[] = [
      this.$refs.persona as HTMLImageElement,
      this.$refs.personaText as HTMLImageElement,
      this.$refs.shadow as HTMLImageElement,
      this.$refs.shadowText as HTMLImageElement,
      this.$refs.self as HTMLImageElement,
      this.$refs.selfText as HTMLImageElement,
      this.$refs.top as HTMLImageElement,
      this.$refs.footer as HTMLImageElement,
    ]

    let unloadedImages = images.length

    images.map(image => {
      image.onload = () => {
        unloadedImages--
        if (!unloadedImages) this.imagesLoaded = true
      }
    })

    // const imageCheck = setInterval(() => {
    // let imagesLoaded = true
    //   images.map(image => {
    //     if(imagesLoaded)
    //       imagesLoaded = image.complete
    //   })
    //   if(imagesLoaded) clearInterval(imageCheck)
    // }, 500)
  }
}
