
import {
  Component,
  Vue,
  Watch,
} from 'vue-property-decorator'
import { base } from '@/store'
import {
  Gender,
  UserAction,
  UserData,
} from '@/@types/types'

import NextButton from '@/components/NextButton.vue'

@Component({
  components: { NextButton },
})
export default class Welcome extends Vue {
  re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  userData = new UserData()
  checked = false

  next() {
    if (this.isValid) {
      // submit to summary
      base.submitForm({
        name: this.userData.name,
        email: this.userData.email,
        age: this.userData.age,
        gender: this.userData.gender,
        domicile: this.userData.domicile,
        info_from: this.userData.info_from,
      })
      // submit to state history
      base.submitAction({
        type: UserAction.submit_form,
      })
    } else this.checked = true
  }

  get emailIsValid(): boolean {
    if (this.userData.email) {
      return this.re.test(this.userData.email.toLowerCase())
    }
    return false
  }
  get nameIsValid() {
    return (
      this.userData.name && this.userData.name.length > 2
    )
  }
  get ageIsValid() {
    if (!this.userData.age) return false
    if (typeof this.userData.age == 'string') return false
    return !!this.userData.age
  }
  get genderIsValid() {
    return !!this.userData.gender
  }
  get domicileIsValid() {
    return !!this.userData.domicile
  }
  get infoIsValid() {
    return (
      this.userData.info_from &&
      this.userData.info_from.length > 2
    )
  }

  isValid = false
  @Watch('userData', { deep: true })
  checkValidity(val: UserData) {
    if (
      base.freeMode ||
      (this.emailIsValid &&
        this.nameIsValid &&
        this.ageIsValid &&
        this.genderIsValid &&
        this.domicileIsValid &&
        this.infoIsValid)
    )
      this.isValid = true
    else this.isValid = false
  }

  get isFree() {
    return base.freeMode
  }
  @Watch('isFree')
  skipValidation(val: boolean) {
    if (val) this.isValid = true
  }

  mounted() {
    base.setBackgroundColor('white')
  }
}
