
import {
  Component,
  Vue,
  Watch,
} from 'vue-property-decorator'
import { base } from '@/store'

import { passCodes } from '@/configs/pageConfig'
import { UserAction, Area } from '@/@types/types'

@Component
export default class Instruction extends Vue {
  get area() {
    return base.currentPageConfig.area
  }
  get correctPasscode() {
    if (this.area) return passCodes[this.area]
    return null
  }
  get instruction(): string[] {
    switch (this.area) {
      case Area.persona:
        return [
          'Bacalah instruksi di',
          'ruangan pertama',
          '(ruang persona).',
        ]
      case Area.shadow:
        return [
          'Bacalah instruksi di',
          'ruangan kedua',
          '(ruang the shadow).',
        ]
      case Area.self:
        return [
          'Bacalah instruksi terlebih',
          'dahulu di ruangan ketiga',
          '(ruang the self).',
        ]
      default:
        return ['']
    }
  }

  passcode: string | null = null
  @Watch('passcode')
  checkPasscode(val: string) {
    if (
      val &&
      this.correctPasscode &&
      val.toLowerCase() ==
        this.correctPasscode.toLowerCase()
    )
      base.submitAction({
        type: UserAction.input_passcode,
      })
  }
  mounted() {
    base.setBackgroundColor(
      this.area != 'shadow' ? 'white' : 'black'
    )
  }
}
