
import { Component, Vue } from 'vue-property-decorator'
import { base } from '@/store'
import {
  Archetype,
  Color,
  ArchetypeColor,
  ArchetypeName,
  ShadowName,
} from '../@types/archetype'
import { UserAction } from '../@types/types'

import GraphLabel from '@/components/GraphLabel.vue'
import NextButton from '@/components/NextButton.vue'

@Component({ components: { GraphLabel, NextButton } })
export default class GraphInstruction extends Vue {
  selectedMult = 3
  labelFraction = 3
  fraction =
    100 / (this.labelFraction + 11 + this.selectedMult)

  stuckSize = this.fraction * 2.8
  halfSelected = (this.selectedMult * this.fraction) / 2

  archetypeNames = ArchetypeName
  shadowNames = ShadowName

  getColor(arch: Archetype): Color {
    return Color[ArchetypeColor[arch]]
  }

  get archetypes(): string[] {
    return Object.keys(Archetype)
  }
  get topArchetypes() {
    return base.summary.top_archetypes
  }
  get persona() {
    return this.topArchetypes.persona
  }
  get shadow() {
    return this.topArchetypes.shadow
  }
  get self() {
    return this.topArchetypes.self
  }

  get stickerColorName() {
    return ArchetypeColor[this.persona]
  }
  get stickerColor() {
    return Color[this.stickerColorName]
  }

  get selfIdx(): number {
    let selfIdx = -1
    this.archetypes.map((arch, idx) => {
      if (this.self == arch) selfIdx = idx
    })
    return selfIdx
  }
  get widths(): number[] {
    const widths: number[] = []
    this.archetypes.map((arch, idx) => {
      widths.push(
        this.fraction *
          (this.selfIdx == idx ? this.selectedMult : 1)
      )
    })
    return widths
  }
  get xs(): number[] {
    let lastX = this.fraction * this.labelFraction
    const xs: number[] = []
    this.widths.map(width => {
      xs.push(lastX)
      lastX += width
    })
    return xs
  }

  get shadowIdx(): number {
    let shadowIdx = -1
    this.archetypes.map((arch, idx) => {
      if (this.shadow == arch) shadowIdx = idx
    })
    return shadowIdx
  }
  get heights(): number[] {
    const heights: number[] = []
    this.archetypes.map((arch, idx) => {
      heights.push(
        this.fraction *
          (this.shadowIdx == idx ? this.selectedMult : 1)
      )
    })
    return heights
  }
  get ys(): number[] {
    let lastY = this.fraction * this.labelFraction
    const ys: number[] = []
    this.heights.map(width => {
      ys.push(lastY)
      lastY += width
    })
    return ys
  }

  next() {
    // submit to state history
    base.submitAction({
      type: UserAction.press_next,
    })
  }

  mounted() {
    base.setBackgroundColor('white')
  }
}
