function setContrast(question: IQuestionRaw) {
  const result: IQuestion = {
    answers: question.answers,
    ageRange: question.ageRange,
    contrasts: {
      Ca: 0,
      Ev: 0,
      In: 0,
      He: 0,
      Ex: 0,
      Re: 0,
      Lo: 0,
      Cr: 0,
      En: 0,
      Sa: 0,
      Ma: 0,
      Ru: 0,
    },
  }

  for (const arch in result.contrasts) {
    const archetype = arch as Archetype

    for (let a = 0; a < result.answers.length; a++) {
      const archA = result.answers[a].scores[archetype] || 0

      for (let b = a + 1; b < result.answers.length; b++) {
        const archB =
          result.answers[b].scores[archetype] || 0

        result.contrasts[archetype] += Math.abs(
          archA - archB
        )
      }
    }
  }

  return result
}

const answers: Record<string, IQuestion[]> = {}

for (const category in rawAnswers) {
  answers[category] = []
  rawAnswers[category].map(questionGroup => {
    answers[category].push(setContrast(questionGroup))
  })
}

export default answers
