
import {
  Component,
  Vue,
  Prop,
} from 'vue-property-decorator'

import {
  PersonaQuestionColor,
  questions,
} from '../@types/archetype'

@Component
export default class QuestionPath extends Vue {
  @Prop({ type: Function, default: () => undefined })
  next!: () => void

  @Prop({ type: Number, default: 0 })
  questionNumber!: number

  @Prop({ type: Number, default: -1 })
  prevQuestionIdx!: number

  @Prop({ type: Number, default: 0 })
  questionIdx!: number

  pathLoaded = false

  questions = questions
  personaQuestionColor = PersonaQuestionColor

  get questionColor() {
    return this.personaQuestionColor[
      this.questions[this.questionIdx][1]
    ]
  }
}
