
import {
  Component,
  Vue,
  Prop,
} from 'vue-property-decorator'

import {
  IAnswer,
  PersonaQuestionColor,
  questions,
} from '../@types/archetype'

@Component
export default class AnswerSet extends Vue {
  @Prop({ type: Array, default: () => [] })
  answers!: IAnswer[]

  @Prop({ type: Function, default: () => undefined })
  pickAnswer!: (answer: IAnswer, idx: number) => void

  @Prop({ type: Number, default: 0 })
  questionIdx!: number

  pathLoaded = false

  showDescriptions = false
  get descriptionsAvailable() {
    return (
      this.answers &&
      ((this.answers[0] && this.answers[0].description) ||
        (this.answers[1] && this.answers[1].description))
    )
  }

  questions = questions
  personaQuestionColor = PersonaQuestionColor

  get questionColor() {
    return this.personaQuestionColor[
      this.questions[this.questionIdx][1]
    ]
  }
}
