
import {
  Component,
  Vue,
  Watch,
} from 'vue-property-decorator'
import { shuffle } from '@/utils'
import { base } from '@/store'

import {
  Archetype,
  IPhase,
  IScore,
  IAnswer,
  IQuestion,
  PersonaQuestion,
  questions,
} from '../@types/archetype'

import { pageConfigs } from '../configs/pageConfig'
import { phases } from '@/configs/persona'
import allAnswers from '@/configs/answers'

import {
  IArchetypeScores,
  UserAction,
  PersonalityPart,
  Change,
} from '../@types/types'

import AnswerSet from '@/components/AnswerSet.vue'
import QuestionPath from '@/components/QuestionPath.vue'

@Component({
  components: { AnswerSet, QuestionPath },
})
export default class PersonaAnswers extends Vue {
  nQuestions = 10
  prevQuestionIdx = -1
  questionIdx = 0
  questions = questions

  availableAnswers: Record<
    string,
    IQuestion[]
  > = Object.assign({}, allAnswers)
  phaseConfig: IPhase = phases[0]

  scoresArray: IScore[] = []
  sortedScores: IScore[] = []

  onPath = true
  toAnswers() {
    this.onPath = false
  }
  setComponent?: number
  component?: string = 'questionPath'

  @Watch('onPath', {
    immediate: true,
  })
  onOnPath(val: boolean, oldVal: boolean) {
    if (val != oldVal) {
      this.component = undefined
      clearTimeout(this.setComponent)
      this.setComponent = setTimeout(() => {
        this.component = val ? 'questionPath' : 'answerSet'
      }, 1100)
    }
  }

  get pageConfig() {
    return base.currentPageConfig
  }
  get questionNumber() {
    return this.pageConfig && this.pageConfig.number
  }
  get userAge() {
    return base.summary.user_data.age
  }

  // scores = {}
  get scores() {
    if (base.currentState) {
      return base.currentState.persona_scores
    }
    return null
  }

  get finished() {
    return (
      this.questionNumber &&
      this.questionNumber >= this.nQuestions
    )
  }
  get focus() {
    // if all
    if (
      this.phaseConfig.focusBetween ==
      this.scoresArray.length
    )
      return this.scoresArray
    // else filter top archetypes to focus on
    return this.sortedScores.slice(
      0,
      this.phaseConfig.focusBetween
    )
  }
  get targets() {
    const targets: { [arch in Archetype]?: number } = {}

    // set target for each archetype from the delta to target
    this.focus.map(score => {
      targets[score.archetype] =
        this.phaseConfig!.target - score.score
    })

    return targets
  }
  get currentQuestion() {
    return questions[this.questionIdx]
  }
  get answers() {
    if (this.availableAnswers) {
      const question = this.availableAnswers[
        this.currentQuestion[0]
      ]
      let fitnesses: { idx: number; fitness: number }[] = []

      question.map((question, idx) => {
        fitnesses.push({
          idx,
          fitness: this.scoreFitness(
            question,
            this.targets
          ),
        })
      })

      fitnesses = fitnesses.sort((A, B) => {
        return A.fitness > B.fitness ? -1 : 1
      })

      const pickedIdx = fitnesses[0].idx
      const pickedAnswerGroup = question[pickedIdx]
      question.splice(pickedIdx, 1)

      const answers: IAnswer[] = []
      let shift = 0
      shuffle(pickedAnswerGroup.answers).map(
        (answer, idx) => {
          if (idx == this.questionIdx) shift = 1
          answers[idx + shift] = answer
        }
      )

      return answers
    }
    return []
  }

  @Watch('questionNumber')
  onQuestionNumber(val: number) {
    let idx = 0

    // if starting
    if (val == 0 || !this.phaseConfig) {
      idx = 0
    }
    // if passed the current "until", select next
    else if (val > this.phaseConfig.until)
      idx = this.phaseConfig.idx + 1

    // set config
    this.phaseConfig = phases[idx]
  }
  @Watch('scoresArray', {
    deep: true,
    immediate: true,
  })
  onScoresArray(val: IScore[]) {
    this.sortedScores = [...val].sort((A, B) => {
      return A.score > B.score ? -1 : 1
    })
  }

  reset() {
    // reset answers
    this.questionIdx = 0
  }
  scoreFitness(
    question: IQuestion,
    targets: Partial<IArchetypeScores>
  ) {
    let fitness = 0

    // score from contrast and current target
    for (const key in question.contrasts) {
      const arch = key as Archetype
      const contrast = question.contrasts[arch]
      fitness -=
        Math.abs(contrast! - (targets[arch] || 0)) ** 3
    }

    // severely reduce the fitness answers of non-matching age range
    if (question.ageRange && this.userAge) {
      const ageMatched =
        this.userAge >= question.ageRange[0] &&
        this.userAge <= question.ageRange[1]
      if (!ageMatched) fitness -= 100
    }

    return fitness
  }
  pickAnswer(answer: IAnswer, idx: number) {
    const changes: Change[] = []
    //  TODO how to save the question color to localstorage
    for (const key in answer.scores) {
      const arch = key as Archetype
      const score = answer.scores[arch]

      changes.push([
        PersonalityPart.persona,
        [arch, score || 0],
      ])
    }

    this.onPath = true

    base.submitAction({
      type: UserAction.answer_question,
      page_detail: idx,
      info: answer.text,
      changes,
    })

    this.prevQuestionIdx = this.questionIdx
    this.questionIdx = idx
    // this.questionNumber++
  }
  mounted() {
    this.questionIdx =
      base.currentState.page_detail != undefined
        ? base.currentState.page_detail
        : Math.floor(Math.random() * 5)

    base.setBackgroundColor('white')
  }
}
